
.enemy-grid__cell {
  display: inline-block;
  width: 60px;
  height: 60px;
  border: black solid 1px;
  line-height: 60px;
  text-align: center;
}
.enemy-grid__cell--fog-of-war {
  background-color: grey;
}
.enemy-grid__cell--hit {
  background-color: red;
}
.enemy-grid__cell--miss {
  background-color: blue;
}



